.carousel {
  width: 100%;
  height: auto;
  /* height: 100%; */
  border: 1px solid #e1e1e1;
  position: relative;
}

.slider {
  width: 100%;
  /* height: 100%; */
  /* position: relative; */
}

.slider section {
  position: absolute;
  top: 0px;
  left: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  /* height: 90vh; */
  /* background-color: white; */
  z-index: 0;
  opacity: 0;
  transition: all 3s;
}

.slider section:nth-child(1) {
  opacity: 1;
  z-index: 1;
  /* background-image: url('../../asserts/images/plant1.jpg'); */
  /* background-size: 100%; */
  /* background-repeat: no-repeat; */
}
.slider section > img {
  position: relative;
  /* background-color: rgba(163, 57, 57, 0.637); */
  /* opacity: .5; */
}

