.slider_img_content {
  color: white;
  font-weight: bold;
  position: absolute;
  text-align: center;
  font-size: 40px;
  font-family: "Redressed", cursive;
}

.next {
  position: absolute;
  top: 325px;
  right: 10px;
  z-index: 2;
  cursor: pointer;
}
/* .next :hover{
    background-color: #751717 !important;
    border-radius: 50%;
    padding: 5px;
    border: 1px solid #1e1e1e !important;
} */
.back {
  position: absolute;
  top: 325px;
  left: 10px;
  z-index: 2;
  cursor: pointer;
}
.info_div_button {
  font-size: 30px;
  background: transparent;
  transition: 0.5s;
  color: white;
  padding: 2px 20px;
  width: 180px;
}
.info_div_button:hover {
  color: black;
  background: white;
}
@media (max-width: 1024px) and (min-width: 901px) {
  .next {
    top: 240px;
  }
  .back {
    top: 240px;
  }
}

@media (max-width: 900px) and (min-width: 768px) {
  .next {
    top: 214px;
  }
  .back {
    top: 214px;
  }
}

